export default {
  name: 'noticeDetail',
  data () {
    return {
      noticeInfo: null
    }
  },
  mounted () {
    this.$http(this.$urlPath.noticeDetails, {
      identity: this.$route.query.identity,
      store_id: this.$route.query.storeId
    }, '', (data) => {
      this.noticeInfo = data.data
    }, (errorCode, error) => {
      this.$toast('加载失败')
    })
  }
}
